@font-face {
  font-family: "swiper-icons";
  src: url("data:application/font-woff;charset=utf-8;base64, ");
  font-weight: 400;
  font-style: normal;
}

:root {
  --swiper-theme-color: #dcdcdc;
  --swiper-navigation-size: 12px;
}

.swiper,
swiper-container {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  z-index: 1;
  display: block;
}

.swiper-wrapper {
  max-width: 300px;
  min-height: 300px;
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
  box-sizing: content-box;
  
  @include onTablet {
    max-width: 465px;
    min-height: 465px;
  }
}

.swiper-slide,
swiper-slide {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform;
  display: block;
}

.swiper-button-prev,
.swiper-button-next {
  display: flex;
  align-items: center;
  justify-content: center;
  width: calc(var(--swiper-navigation-size) / 44 * 27);
  height: var(--swiper-navigation-size);
  position: absolute;
  top: var(--swiper-navigation-top-offset, 50%);
  font-family: swiper-icons;
  font-size: var(--swiper-navigation-size);
  font-weight: bold;
  z-index: 10;
  cursor: pointer;
  color: var(--swiper-navigation-color, var(--swiper-theme-color));
  padding: 18px;
  border-radius: 20px;
  border: 2px solid $grey-color;
  transition: 0.3s;

  &:hover {
    --swiper-theme-color: #8c8c8c;
    border-color: #8c8c8c;
  }
}

.swiper-button-prev,
.swiper-rtl .swiper-button-next {
  left: var(--swiper-navigation-sides-offset, 10px);
  right: auto;
}
.swiper-button-prev:after,
.swiper-rtl .swiper-button-next:after {
  content: 'prev';
}
.swiper-button-next,
.swiper-rtl .swiper-button-prev {
  right: var(--swiper-navigation-sides-offset, 10px);
  left: auto;
}
.swiper-button-next:after,
.swiper-rtl .swiper-button-prev:after {
  content: 'next';
}


.swiper {
  &__pagination {
    @extend %subtitle;
  }

  &__title {
    @extend %title;

    font-size: 24px;
    margin-bottom: 20px;
  }
}
